import React, { useState } from "react";
import "@mantine/core/styles.css";
import {
  MantineProvider,
  createTheme,
  Accordion,
  TextInput,
  NumberInput,
  Text,
  Button,
  Group,
  Box,
  Container,
  Textarea,
  Image,
  Drawer,
  ActionIcon,
} from "@mantine/core";
import { IconEdit, IconMenu2, IconX } from "@tabler/icons-react";


/* ============================================================
   GLOBAL CONFIG
============================================================ */
const globalLocks = ["icon", "image", "link"];
const customLocksTags = [
  "$$typeof",
  "key",
  "ref",
  "_owner",
  "type",
  "icon",
  "required",
];
const customLocksLinks = [
  "ctaHref",
  "secondaryHref",
  "path",
  "navLinks",
  "quickLinks",
  "rows",
  "placeholder",
  "nominateFileUpload",
  "processNominationForm",
];
const customLocks = [
  ...customLocksTags,
  ...customLocksLinks,
  "sectionId",
  "id",
];

/* ============================================================
   HELPERS
============================================================ */
function getAtIndex(obj: any, index: number): any {
  if (!obj) return undefined;
  if (Array.isArray(obj)) return obj[index];
  if (obj.array && Array.isArray(obj.array)) return obj.array[index];
  return undefined;
}

function getChildNodeByPath(obj: any, path: (string | number)[]): any {
  if (!obj || typeof obj !== "object" || !Array.isArray(path)) return undefined;
  let current = obj;
  for (const key of path) {
    const normalizedKey =
      typeof key === "string" && /^\d+$/.test(key) ? Number(key) : key;
    if (
      current === null ||
      typeof current !== "object" ||
      !(normalizedKey in current)
    ) {
      return undefined;
    }
    current = current[normalizedKey];
  }
  return current;
}

/* ============================================================
   TYPES
============================================================ */
type JsonValue =
  | string
  | number
  | boolean
  | null
  | JsonValue[]
  | { [key: string]: JsonValue };

interface JsonEditorProps {
  data: JsonValue;
  settingsData?: any;
  path?: string[];
  onChange: (path: string[], value: JsonValue) => void;
}

/* ============================================================
   UTILITIES
============================================================ */
function isGloballyLocked(settingsNode?: any): boolean {
  const type = settingsNode?.type;
  return !!type && globalLocks.includes(type);
}

function isCustomLocked(key: string | undefined | null): boolean {
  if (typeof key !== "string") return false;
  return customLocks.some(
    (lock) => typeof lock === "string" && lock.toLowerCase() === key.toLowerCase()
  );
}



function hasAnyVisibleChild(value: any, subSettings: any): boolean {
  if (!value || typeof value !== "object") return false;

  if (!Array.isArray(value) && "array" in value && Array.isArray(value.array)) {
    const arr = value.array;
    const arrSettings = subSettings?.array;
    return arr.some((item: any, idx: number) => {
      const itemSettings = Array.isArray(arrSettings)
        ? arrSettings[idx]
        : arrSettings;
      const locked =
        itemSettings?.settings?.locked ||
        isGloballyLocked(itemSettings?.settings);
      return !locked;
    });
  }

  const entries = Object.entries(value).filter(([k]) => k !== "settings");
  for (const [childKey] of entries) {
    const childSettings = subSettings?.[childKey];
    const childLocked =
      childSettings?.settings?.locked ||
      isGloballyLocked(childSettings?.settings) ||
      isCustomLocked(childKey);
    if (!childLocked) return true;
  }

  return false;
}



/* ============================================================
   JSON EDITOR CORE
============================================================ */
const JsonEditor: React.FC<JsonEditorProps> = ({
  data,
  settingsData,
  path = [],
  onChange,
}) => {
  // --- handle wrapped arrays ---
  if (
    data &&
    typeof data === "object" &&
    !Array.isArray(data) &&
    "array" in (data as any) &&
    Array.isArray((data as any).array)
  ) {
    const wrapper: any = data;
    const array = wrapper.array;
    const arraySettings = settingsData?.settings || wrapper.settings || {};
    const label = arraySettings?.label || path[path.length - 1] || "Array";
    const locked =
      arraySettings?.locked || isGloballyLocked(arraySettings) || false;
    if (locked) return null;

    const hasVisible = array.some((item: any, idx: number) => {
      const itemSettings = Array.isArray(settingsData?.array)
        ? settingsData?.array[idx]
        : settingsData?.array;
      const itemLocked =
        itemSettings?.settings?.locked ||
        isGloballyLocked(itemSettings?.settings);
      return !itemLocked;
    });
    if (!hasVisible) return null;

    return (
      <Accordion variant="separated" radius="md" multiple defaultValue={[label]}>
        <Accordion.Item value={label}>
          <Accordion.Control>
            <Text fw={600}>{label}</Text>
          </Accordion.Control>
          <Accordion.Panel>
            <Box pl="md">
              <JsonEditor
                data={array}
                settingsData={settingsData?.array}
                path={[...path, "array"]}
                onChange={onChange}
              />
            </Box>
          </Accordion.Panel>
        </Accordion.Item>
      </Accordion>
    );
  }

  // --- handle arrays ---
  if (Array.isArray(data)) {
    const visibleItems = data
      .map((item, index) => {
        const itemSettings = Array.isArray(settingsData)
          ? settingsData[index]
          : settingsData;
        const isObjectItem =
          item && typeof item === "object" && !Array.isArray(item);
        const childKeys = isObjectItem
          ? Object.keys(item as any).filter((k) => k !== "settings")
          : [];

        const itemSettingsLabel =
          isObjectItem && (item as any).settings?.label
            ? String((item as any).settings.label)
            : undefined;
        const schemaSettingsLabel =
          itemSettings?.settings?.label
            ? String(itemSettings.settings.label)
            : undefined;

        let firstChildLabel: string | undefined;
        if (isObjectItem) {
          for (const k of childKeys) {
            const v: any = (item as any)[k];
            if (
              v &&
              typeof v === "object" &&
              "settings" in v &&
              typeof v.settings?.label === "string"
            ) {
              firstChildLabel = v.settings.label;
              break;
            }
          }
        }

        const label =
          itemSettingsLabel ||
          schemaSettingsLabel ||
          firstChildLabel ||
          `Item ${index + 1}`;
        const locked =
          itemSettings?.settings?.locked ||
          isGloballyLocked(itemSettings?.settings);
        if (locked) return null;

        return (
          <Accordion.Item key={index} value={`${path.join(".")}.${index}`}>
            <Accordion.Control>{label}</Accordion.Control>
            <Accordion.Panel>
              <Box pl="md" pt="sm">
                <JsonEditor
                  data={item}
                  settingsData={itemSettings}
                  path={[...path, String(index)]}
                  onChange={onChange}
                />
              </Box>
            </Accordion.Panel>
          </Accordion.Item>
        );
      })
      .filter(Boolean);

    if (visibleItems.length === 0) return null;

    return (
      <Accordion
        variant="contained"
        radius="md"
        multiple
        defaultValue={data.map((_, i) => `${path.join(".")}.${i}`)}
      >
        {visibleItems}
      </Accordion>
    );
  }

  // --- handle objects ---
  if (typeof data === "object" && data !== null) {
    const currentSettings = settingsData?.settings || {};
    const locked =
      currentSettings?.locked || isGloballyLocked(currentSettings);
    const currentKey = path[path.length - 1] || "";
    if (locked || isCustomLocked(currentKey)) return null;

    const entries = Object.entries(data).filter(([key]) => key !== "settings");
    const visibleChildren = entries
      .map(([key, value]) => {
        const subSettings = settingsData?.[key];
        const subIsImageObject = subSettings?.settings?.type === "image";
        const subLocked =
          subSettings?.settings?.locked ||
          isGloballyLocked(subSettings?.settings) ||
          isCustomLocked(key);
        // If the subsection is locked and not an image object, hide it entirely.
        if (subLocked && !subIsImageObject) return null;
        // If it's an object with no visible children and not an image object, hide it.
        if (
          value &&
          typeof value === "object" &&
          !Array.isArray(value) &&
          !hasAnyVisibleChild(value, subSettings) &&
          !subIsImageObject
        ) {
          return null;
        }

        const subLabel = subSettings?.settings?.label || key;
        
        return (
          <Accordion.Item key={key} value={key}>
            <Accordion.Control>
              <Text fw={600}>{subLabel}</Text>
            </Accordion.Control>
            <Accordion.Panel>
              <Box pl="md" pt="sm">
                <JsonEditor
                  data={value}
                  settingsData={subSettings}
                  path={[...path, key]}
                  onChange={onChange}
                />
              </Box>
            </Accordion.Panel>
          </Accordion.Item>
        );
      })
      .filter(Boolean);

    if (visibleChildren.length === 0) return null;

    return (
      <Accordion variant="separated" radius="md" multiple>
        {visibleChildren}
      </Accordion>
    );
  }

  // --- handle primitives ---
  const currentSettings = settingsData?.settings;
  const keyName = path[path.length - 1];

  const label = currentSettings?.label || keyName;
  const isImageUrl =
    typeof data === "string" &&
    data.startsWith("http") &&
    /\.(png|jpg|jpeg|webp|gif)$/i.test(String(data));

  const selfLocked =
    currentSettings?.locked ||
    isGloballyLocked(currentSettings) ||
    isCustomLocked(keyName);
  if (selfLocked) return null;
  const inputDisabled = !!selfLocked;

  const isLongText = typeof data === "string" && data.length > 100;

  if (typeof data === "number") {
    return (
      <NumberInput
        value={data}
        onChange={(val) => onChange(path, Number(val))}
        min={0}
        radius="md"
        size="sm"
        mb="xs"
      />
    );
  }


  if (isLongText) {
    return (
      <Textarea
        value={String(data ?? "")}
        onChange={(e) => onChange(path, e.currentTarget.value)}
        radius="md"
        size="sm"
        autosize
        minRows={3}
        mb="xs"
        disabled={inputDisabled}
      />
    );
  }

  return (
    <TextInput
      value={String(data ?? "")}
      onChange={(e) => onChange(path, e.currentTarget.value)}
      radius="md"
      size="sm"
      mb="xs"
      disabled={inputDisabled}
    />
  );
};

/* ============================================================
   MAIN PAGE
============================================================ */
const JsonEditorPage: React.FC<{ initialData: JsonValue; settingsData: any; setContent: (content: any) => void }> = ({
  initialData,
  settingsData,
  setContent,
}) => {
 const [jsonData, setJsonData] = useState<JsonValue>(initialData);

React.useEffect(() => {
  setJsonData(initialData);
}, [initialData]);


  //  keep local editor data synced with props
  React.useEffect(() => {
    setJsonData(initialData);
  }, [initialData]);

  const handleChange = (path: string[], value: JsonValue) => {
    setJsonData((prev) => {
      const newData = JSON.parse(JSON.stringify(prev, (_key, value) => {
        if (
          typeof value === "function" ||
          typeof value === "symbol" ||
          (value && value.$$typeof)
        ) {
          return undefined;
        }
        return value;
      }));
      let current: any = newData;
      for (let i = 0; i < path.length - 1; i++) current = current[path[i]];
      current[path[path.length - 1]] = value;
      return newData;
    });
  };

  const handleConsole = () => {
    console.log(" Updated JSON:", jsonData);
    setContent(jsonData);
  };

  return (
    <Container size="lg" py="xl">
      <Group justify="space-between" mb="lg">
        <Text size="xl" fw={700}>
          Dynamic JSON Editor
        </Text>
        <Button leftSection={<IconEdit size={18} />} onClick={handleConsole}>
          Apply Changes
        </Button>
      </Group>
      <JsonEditor data={jsonData} settingsData={settingsData} onChange={handleChange} />
    </Container>
  );
};


/* ============================================================
   WRAPPER WITH FLOATING HAMBURGER
============================================================ */
const theme = createTheme({
  fontFamily: "Inter, sans-serif",
  defaultRadius: "md",
});

export default ({ content, setContent, toggle }:any) => {
  const [opened, setOpened] = useState(false);
  if(toggle !== undefined && toggle === false ) return <></>

  return (
    <MantineProvider theme={theme} defaultColorScheme="light">
      {/* Floating Hamburger on LEFT side */}
      <ActionIcon
        variant="filled"
        radius="xl"
        size="lg"
        color="lime"
        onClick={() => setOpened(true)}
        style={{
          position: "fixed",
          top: "50%",
          left: "20px",
          transform: "translateY(-50%)",
          zIndex: 999999, // <-- bump this high enough
          boxShadow: "0 4px 12px rgba(0,0,0,0.25)",
        }}
      >
        <IconMenu2 size={22} />
      </ActionIcon>


      {/* Drawer Panel - slides from LEFT */}
      <Drawer
        opened={opened}
        onClose={() => setOpened(false)}
        position="left"
        size="80%"
        overlayProps={{ opacity: 0.3, blur: 2 }}
        title={
          <Group justify="space-between" align="center" w="100%">
            <Text fw={700}>JSON Editor Panel</Text>
            <ActionIcon onClick={() => setOpened(false)}>
              <IconX size={20} />
            </ActionIcon>
          </Group>
        }
      >
        <Box style={{ overflowY: "auto", height: "calc(100vh - 100px)" }}>
          <JsonEditorPage initialData={content} settingsData={undefined} setContent={setContent} />
        </Box>
      </Drawer>
    </MantineProvider>
  );
};
